export default{
  showLoading:({commit})=>{
    commit('showLoading')
  },
  hideLoading:({commit})=>{
    commit('hideLoading')
  },
  showHeader:({commit})=>{
    commit('showHeader')
  },
  hideHeader:({commit})=>{
    commit('hideHeader')
  }
}



