<template>
  <div id="nowintheater">
    <h1>影院热映</h1>
    <section id="list" class="grid">
      <a v-for="(item,index) in arrList.subject_collection_items" v-bind:href="item.url" class="item">
        <div class="cover">
          <div class="wp ratio3_4">
            <img v-bind:src="item.cover.url" alt="寻梦环游记" class="img-show" style="width: 100%;">

          </div>
        </div>
        <div class="info">
          <h4></h4>
          <h3>{{item.title}}</h3>
          <p class="rank" v-if="item.rating == null">
            <span>暂无评分</span>
          </p>
          <p class="rank" v-else>
            <span class="rating-stars">
              <span class="rating-star rating-star-small-full"></span>
              <span class="rating-star rating-star-small-full"></span>
              <span class="rating-star rating-star-small-full"></span>
              <span class="rating-star rating-star-small-full"></span>
              <span class="rating-star rating-star-small-full"></span>
            </span>
            <span id="score">{{item.rating['value']}}</span>


          </p>
          <p class="meta">美国 / 喜剧 动画 冒险 / 李·昂克里奇 / 安东尼·冈萨雷斯 盖尔·加西亚·贝纳尔</p>
          <cite></cite>
        </div>
      </a>
    </section>
  </div>
</template>
<script>
  import $ from 'jquery'
  export default{
    data(){
      return{
        arrList:[]
      }
    },
    mounted(){
      this.getData();
    },
    methods:{
      getData(){
        var _this=this;
        this.$http.jsonp('https://m.douban.com/rexxar/api/v2/subject_collection/movie_showing/items',
        {
          params:{
            os:'ios',
            for_mobile:'1',
            callback:'jsonp1',
            start:'0',
            count:'18',
            loc_id:'108288',
            _:'1511857592457'
          }
        }).then(function (res) {
            console.log(res.body.subject_collection_items)
          _this.arrList=res.body;
          $(document).ready(function(){
            var score = $('#score').html()
            var t=(score/2).toFixed(0)
            for (var i=0 ;i<t;i++){
              $('.rating-stars .rating-star-small-full').eq(i).css("background-image","url('../assets/img/star1.png')")
            }
          })

        }).catch(function (err) {
          console.log(err)
        })
      }
    }
  }
</script>
<style>
#nowintheater{
  flex:1;
  padding-left: 2%;
  padding-right: 2%;
  max-width: 650px;
  overflow-x: hidden;
}
#nowintheater>h1{
    margin: 0.67em 0;
    font-size: 24px;
    font-weight: normal;
    box-sizing: border-box;
    max-width: 660px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 6px;
    padding-left: 4%;
  }
  #list .item {
    text-decoration: none;
    color: #9b9b9b;
  }
  .grid .item {
    float: left;
    box-sizing: border-box;
    width: 33.33333%;
    padding-left: 4%;
    padding-right: 4%;
    margin-bottom: 20px;
    overflow: hidden;
  }
  .grid .item .cover {
    min-height: 87px;
    overflow: hidden;
    position: relative;
  }
  .grid .ratio1_1, .grid .ratio3_4 {
    position: relative;
  }
  #list .item .img-show {
    opacity: 1;
  }
  #list .item img {
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
  }
  .grid .item img {
    display: block;
    position: absolute;
    left: -100%;
    right: -100%;
    top: -100%;
    bottom: -100%;
    margin: auto;
    min-height: 100%;
    min-width: 100%;
  }
  .grid .ratio1_1:before, .grid .ratio3_4:before {
    content: "";
    display: block;
    padding-top: 100%;
  }
  .grid .ratio3_4:before {
    padding-top: 143%;
  }
  .grid .item .info {
     height: 45px;
     overflow: hidden;
   }
  #list h3, #list h4, #list p {
    margin: 0;
  }
  .grid .item h4, .grid .item cite, .grid .item .meta {
    display: none;
  }
  .grid .item h3 {
    font-size: 13px;
    font-weight: normal;
    padding: 5px 0 0;
    color: #494949;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
  .grid .item .rank {
     height: 16px;
     font-size: 12px;
     padding-top: 3px;
     text-align: center;
   }
  .rating-stars {
    display: inline-block;
    vertical-align: middle;
  }
  .rating-stars .rating-star {
    display: inline-block;
    margin-right: 1px;
    background-color: transparent;
    background-repeat: no-repeat;
  }
  .rating-star-small-full {
    background-image: url('../assets/img/star0.png');
  }
  .rating-star-small-full, .rating-star-small-half, .rating-star-small-gray {
    width: 10px;
    height: 10px;
    background-size: 10px 10px;
  }
  #list .item cite {
    font-style: normal;
  }
</style>



