export default{
  showLoading:(state)=>{
    return state.loading;
  },
  hideLoading:(state)=>{
    return state.loading;
  },
  showHeader:(state)=>{
    return state.header;
  },
  hideHeader:(state)=>{
    return state.header;
  }
}



