<template>
  <div id="header">
    <router-link to="/home" tag='h1' class="title">

    </router-link>
    <ul class="nav">
      <li>
        <router-link style="color: #2384E8" to="/movie">
          电影
        </router-link>
      </li>
      <li>
        <router-link style="color: #9F7860" to="/books">
          图书
        </router-link>
      </li>
      <li>
        <router-link style="color: #E4A813" to="/broadcast">
