<style scoped>
  #header{
    border-bottom: 1px solid #f3f3f3;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    padding: 0 .18rem;
    background: #fff;
    height: .47rem;
    box-sizing: border-box;
    display: -webkit-box;
    display: flex;
  }
  .title{
    height: .47rem;
    line-height: .47rem;
  }
  .nav{
    display: flex;
    justify-content: space-between;
  }
  .nav>* {
    vertical-align: middle;
  }
  .nav li {
    font-size: 15px;
    margin-right: 19px;
    display: inline-block;
  }
  .title{
    background:url('../assets/img/abimg.png') no-repeat center center ;
    background-size: cover;
    height: .22rem;
    width: .46rem;
    display: inline-block;
  }
  .talion{
    background:url('../assets/img/search1.png') no-repeat center center ;
    background-size: cover;
    width: .24rem;
    height: .18rem;
    margin-top: 4px;
    position: relative;
  }
</style>



