  }
  li{
    width: .24rem;
    height: .24rem;
    background: url("../assets/img/123.png") no-repeat 0 0;
  }
  #detail{
    width: 100%;
  }
  #detail p{
    text-indent: .28rem;
    width: 100%;
  }



</style>



