import getters from './getters'
const state = {
  header:true,
  loading:false
};
const mutations = {
  showLoading(state){
    state.loading = true
  },
  hideLoading(state){
    state.loading = false;
  },
  showHeader(state){
    state.header = true
  },
  hideHeader(state){
    state.header = false;
  }
}
export default{
  state,
  mutations,
  getters
}



